<template>
    <div>
        <van-row type="flex" align="center">
            <van-col span="12">
                <input type="text" placeholder="用户名" v-model="aaa" v-on:input="ccc">
            </van-col>
            <van-col span="12" class="text-right">
                <van-button type="primary" @click="addFn()">新增</van-button>
            </van-col>
        </van-row>
        <table>
            <tr>
                <th width="15%">id</th>
                <th width="25%">用户名</th>
                <th width="25%">性别</th>
                <th>操作</th>
            </tr>
            <tr v-for="(x,index) in myData" :key="x.id">
                <td>{{x.id}}</td>
                <td>{{x.name}}</td>
                <td>{{x.sex}}</td>
                <td><span @click="editFn(index)">编辑</span><span @click="deleteFn(index)">删除</span></td>
            </tr>
            <tr>
                <td colspan="4" v-show="myData.length===0">暂无用户</td>
            </tr>
        </table>

        <van-dialog
                v-model="show"
                show-cancel-button
                v-on:confirm="bbb"
        >
            <van-field
                    v-model="username"
                    label="用户名"
                    placeholder="请输入用户名"
            />
            <div style="font-size:14px;margin:0 15px"><p>选择性别</p>
                <van-radio-group v-model="sex">
                    <van-radio name="男" style="margin-right:30px;">男</van-radio>
                    <van-radio name="女">女</van-radio>
                </van-radio-group>
            </div>
        </van-dialog>
    </div>
</template>

<script>
    let n = 3;
    let state = 0;
    let myIndex = "";
    export default {
        name: "TableOne",
        data: function () {
            let myData = [{id: 1, name: "张三", sex: "男"}, {id: 2, name: "李四", sex: "女"}];
            return {
                myData,
                show: false,
                username: '',
                sex: '男',
                aaa: ''
            }
        },
        methods: {
            deleteFn: function (index) {
                this.$dialog.confirm({
                    title: '操作提示',
                    message: '删除这个用户吗？'
                }).then(() => {
                    this.myData.splice(index, 1);
                    n--;
                }).catch(() => {
                });
            },
            addFn: function () {
                state = 0;
                this.show = true;
            },
            bbb: function () {
                if (state === 0) {
                    this.myData.push({id: n, name: this.username, sex: this.sex});
                    n++;
                } else {
                    this.myData[myIndex].name = this.username;
                    this.myData[myIndex].sex = this.sex;
                }
                this.username = "";
            },
            ccc: function () {
                for (let i = 0; i < this.myData.length; i++) {
                    if (this.myData[i].name === this.aaa) {
                        this.myData = Array(this.myData[i]);
                    }
                }
            },
            editFn: function (index) {
                state = 1;
                this.show = true;
                this.username = this.myData[index].name;
                this.sex = this.myData[index].sex;
                myIndex = index;
            }
        }
    }
</script>

<style scoped lang="scss">
    input {
        width:100%;
        box-sizing:border-box;
    }
    table {
        margin-top:20px;
        width:100%;
        border-collapse:collapse;
        font-size:14px;
        th {
            background-color:#eee;
        }
        th, td {
            text-align:center;
            padding:15px 5px;
            border:1px solid #ccc;
            span {
                &:active {
                    color:red;
                }
                &:first-child {
                    margin-right:10px;
                }
            }
        }
    }
    .van-radio {
        display:inline-block
    }
</style>
